var servicedoc = {};

servicedoc.reload = function() {
    var graphURIs = '';
    $('.service-documentation-page .graphCheckBox').each(function(index, value) {
        if ($(value).is(':checked')) {
            if (graphURIs.length > 0) {
                graphURIs += ' ';
            }
            graphURIs += $(value).attr('value');
        }
    });
    swa.load('main', { graphURIs: '"' + graphURIs + '"' });
};

$(function() {
    var allSelected = false;
    $('.service-documentation-page .service-filter .select-all').click(function() {
        allSelected = !allSelected;
        $('.service-documentation-page .service-filter .graphCheckBox').prop('checked', allSelected);
        $('.service-documentation-page .service-filter .select-all').text(allSelected ? 'Deselect all' : 'Select all');
        servicedoc.reload();
    });
});

export default servicedoc;
