import { axiosInstance } from 'utils';

var tbladmin = {
    /**
     * Hides the EVN Explorer user name and password field (if anonymous)
     */
    hideUserAndPassword: function() {
        $('#evnViewerServerUserName').hide();
        $('#evnViewerServerPassword').hide();
    },

    resetCachedGraph: function(graphURI, buttonId) {
        $('#' + buttonId).attr('disabled', 'disabled');

        return axiosInstance
            .get('swp', {
                params: {
                    _snippet: true,
                    _viewClass: 'tbladmin:ResetCachedGraphService',
                    graph: '<' + graphURI + '>',
                },
            })
            .then((response) => {
                if (response.data) {
                    swa.populateModalDialog('Cache succesfully cleared.', null, null);
                } else {
                    swa.populateModalDialog('Unexpected result: ' + response.data, null, null);
                }
            })
            .catch((err) => {
                swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
            });
    },

    rdbmsVacuum: function() {
        var confirmed = swa.populateConfirmDialog(
            'Are you sure?  This will irrevocably delete data from the database!  Only do this during a maintenance window with no user access!'
        );

        Promise.resolve(confirmed).then(
            function() {
                return axiosInstance
                    .get('swp', {
                        params: {
                            _snippet: true,
                            _viewClass: 'tbladmin:RDBMSVacuumService',
                        },
                    })
                    .then((response) => {
                        if (response.data || response.data == 0) {
                            swa.populateModalDialog('Deleted ' + response.data + ' orphaned nodes.', null, null);
                        } else {
                            swa.populateModalDialog('Unexpected result: ' + response.data, null, null);
                        }
                    })
                    .catch((err) => {
                        swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
                    });
            },
            function(err) {
                //dialog cancelled
            }
        );
    },

    sendProjects: function(progressId) {
        var params = $('#form').serialize();
        let params2 = new URLSearchParams(params);

        swa.openProgressMonitorDialog(progressId, 'Sending Projects...', 'Sending Projects to Other TBL Server...');

        return axiosInstance
            .post(swa.server + 'sendProjects', params2)
            .then(() => {
                swa.closeProgressMonitorDialog();
                swa.populateModalDialog(
                    response.data.projects +
                        ' projects with ' +
                        response.data.files +
                        ' selected files have been sent successfully.',
                    'Success',
                    null
                );
            })
            .catch((err) => {
                swa.closeProgressMonitorDialog();
            });
    },

    /**
     * Toggles the user name field as enabled or disabled,
     * and the password field as hidden or visible
     */
    toggleUserAndPassword: function() {
        var anonymous = $('#evnViewerServerIsAnonymous').val();
        var userName = $('#evnViewerServerUserName').find('.test-evnViewerServerUserName')[0];
        if (anonymous == 'true') {
            $(userName).prop('disabled', true);
            $('#evnViewerServerPassword').hide();
        } else {
            $(userName).prop('disabled', false);
            $('#evnViewerServerPassword').show();
        }
    },

    updateFileCheckBoxes: function(classId, checked) {
        $('.checkboxclass-' + classId).prop('checked', checked);
    },

    /**
     * Checks if we are using legacy Explorer
     */
    useLegacyExplorer: function() {
        return false;
    },

    showClearTarget: function(allowChecked) {
        $('#clearTarget').toggle(allowChecked);
    },

    checkKey: function(e, elem, name, username, url) {
        if (e.keyCode == 13) {
            $('#' + elem.id).blur();
            tbladmin.submitPassword(elem, name, username, url);
            e.preventDefault();
        }
    },

    submitPassword: function(elem, name, username, url) {
        var id = elem.id;
        var pw = $('#' + id).val();
        if (pw != '' && pw != '****') {
            var name = username + '@' + url;
            var data = {};
            data[name] = pw;

            let params2 = new URLSearchParams(data);
            return axiosInstance
                .post('credentialHandler', params2)
                .then(() => {
                    swa.populateModalDialog(name + ' saved.');
                })
                .catch((err) => {
                    swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
                });
        }
    },
};

// This makes sure the "smtp from address" field is either
// empty or a valid email address format.
$(document).on('blur keypress', "input[class$='smtpServerFrom']", function(e) {
    var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    var input = $(this).val();

    if (input.length != 0 || (e.which == 13 && input.length != 0)) {
        if (!re.test(input)) {
            if ($('#smtpFromError').length == 0) {
                $(this).after('<p id="smtpFromError" class="text-danger">Please enter a valid email address</p>');
            }
            $('.save-button').prop('disabled', true);
        } else {
            $('#smtpFromError').remove();
            $('.save-button').prop('disabled', false);
        }
    } else {
        $('#smtpFromError').remove();
        $('.save-button').prop('disabled', false);
    }
});

export default tbladmin;
