import { axiosInstance } from 'utils';

var pwadmin = {
    addPassword: function(userName, url, password) {
        if (userName.length > 0 && url.length > 0 && password.length > 0 && password != '****') {
            var userAtUrl = userName + '@' + url;
            var params = {};
            params[userAtUrl] = password;
            let params2 = new URLSearchParams(params);
            axiosInstance
                .post('credentialHandler', params2)
                .then(() => {
                    window.location = 'swp?_viewClass=pwadmin:PasswordManagerPage';
                })
                .catch((err) => {
                    swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
                });
        }
    },

    submitPassword: function(user, url) {
        var pw = $('#password-field').val();
        if (pw == '') return;

        return axiosInstance
            .post('credentialHandler', { [user + '@' + url]: pw })
            .then(() => {
                Promise.resolve(swa.populateModalDialog('Password saved.')).then(function() {
                    window.location = 'swp?_viewClass=pwadmin:PasswordManagerPage';
                });
            })
            .catch((err) => {
                swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
            });
    },

    removePassword: function(userAtUrl) {
        var removeConfirmed = swa.populateConfirmDialog('Remove secure storage password for ' + userAtUrl + '?');

        Promise.resolve(removeConfirmed).then(
            function() {
                var params = {
                    _viewClass: 'pwadmin:RemoveSecureStoragePasswordService',
                    user: userAtUrl,
                };

                let params2 = new URLSearchParams(params);
                return axiosInstance
                    .post(swa.server + swa.servlet, params2)
                    .then(() => {
                        window.location = 'swp?_viewClass=pwadmin:PasswordManagerPage';
                    })
                    .catch((err) => {
                        swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
                    });
            },
            function(err) {
                //dialog canceled
            }
        );
    },

    initAddPasswordDialog: function() {
        var userName = $('#userName');
        var url = $('#url');
        var password = $('#password');
        $('#addPasswordDialog').dialog({
            autoOpen: false,
            modal: true,
            buttons: {
                'Add Password': function() {
                    pwadmin.addPassword(userName.val(), url.val(), password.val());
                    $(this).dialog('close');
                },
                Cancel: function() {
                    $(this).dialog('close');
                },
            },
            open: function() {
                var thisDialog = this;
                $(thisDialog).keypress(function(key) {
                    if (key.keyCode == $.ui.keyCode.ENTER) {
                        pwadmin.addPassword(userName.val(), url.val(), password.val());
                        $(thisDialog).dialog('close');
                    }
                });
            },
            close: function() {
                userName.val('');
                url.val('');
                password.val('');
            },
        });
    },

    openAddPasswordDialog: function() {
        $('#addPasswordDialog').dialog('open');
    },
};

export default pwadmin;
