import { axiosInstance } from 'utils';

var pgadmin = {
    /**
     * Adds assets to a permission group
     * @param groupName  {String} name of the permission group to add to
     * @param withImports  ???
     */
    addAssetsFromTreeToGroup: function(groupName, withImports) {
        var params, $checkedAssets, assetId;

        if (groupName.length > 0) {
            params = {
                group: groupName,
            };
            // assetIDs
            $checkedAssets = $('.jstree-clicked');

            if ($checkedAssets.length > 0) {
                if (withImports !== undefined && withImports !== null) {
                    params.withImports = withImports;
                }

                if ($checkedAssets.length > 10 || withImports) {
                    swa.insertLoadingIndicator($('#roleEditorAssetList'));
                }

                let params2 = new URLSearchParams(params);

                $checkedAssets.each(function(index, checkedAssetElement) {
                    assetId = $(checkedAssetElement)
                        .parent()
                        .attr('data-resource');
                    params2.append('assets[]', assetId);
                });

                axiosInstance
                    .post(swa.server + 'addAssetsToGroup', params2)
                    .then(() => {
                        swa.load('roleEditorAssetList');
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            }
        }
    },

    addAssetsToGroup: function(assetIds, assetFamilies, groupName, withImports) {
        if (assetIds.length > 0 && assetFamilies.length > 0 && groupName.length > 0) {
            if (withImports != undefined && withImports != null) {
                params.withImports = withImports;
                if (withImports) {
                    swa.insertLoadingIndicator($('#roleEditorAssetList'));
                }
            }

            return axiosInstance
                .get(swa.server + swa.servlet, {
                    params: {
                        _viewClass: 'pgadmin:AddAssetsToGroupService',
                        assets: assetIds,
                        families: assetFamilies,
                        group: groupName,
                    },
                })
                .then((response) => {
                    swa.load('roleEditorAssetList');
                })
                .catch((err) => {
                    swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
                });
        }
    },

    addGroupToRole: function(groupName, roleName) {
        if (groupName.length > 0) {
            if (groupName == '_newGroup_') {
                pgadmin.openCreateNewGroupDialog();
                swa.load('roleEditorAddGroup');
            } else {
                return axiosInstance
                    .get(swa.server + swa.servlet, {
                        params: {
                            _viewClass: 'pgadmin:AddRoleToGroupService',
                            role: roleName,
                            group: groupName,
                        },
                    })
                    .then((response) => {
                        window.location =
                            'swp?_viewClass=pgadmin:RoleEditorPage&selectedRole=' +
                            escape(roleName) +
                            '&selectedGroup=' +
                            escape(groupName);
                    })
                    .catch((err) => {
                        swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
                    });
            }
        }
    },

    addRoleToGroup: function(roleName, groupName) {
        if (roleName.length > 0) {
            var params = {};

            return axiosInstance
                .get(swa.server + swa.servlet, {
                    params: {
                        _viewClass: 'pgadmin:AddRoleToGroupService',
                        role: roleName,
                        group: groupName,
                    },
                })
                .then((response) => {
                    swa.load('groupEditorRoleList');
                    swa.load('groupEditorAddRole');
                })
                .catch((err) => {
                    swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
                });
        }
    },

    /**
     * Enables the "next" and "add" buttons in the dialog for adding assets/resources
     */
    assetTreeCheck: function() {
        var $nextBtn = $('#selectAssetsTreeDialogNext'),
            $addBtn = $('#selectAssetsTreeDialogAddAssets');

        if ($nextBtn.button('instance') !== undefined && $addBtn.button('instance') !== undefined) {
            $nextBtn.button('enable');
            $addBtn.button('enable');
        } else {
            console.warn('No instance of button, cannot enable');
        }
    },

    /**
     * Disables the "next" and "add" buttons in the dialog for adding assets/resources
     */
    assetTreeUncheck: function() {
        var checkedAssetCount = $('.jstree-clicked').length,
            $nextBtn = $('#selectAssetsTreeDialogNext'),
            $addBtn = $('#selectAssetsTreeDialogAddAssets');

        // once the number of checked assets reaches zero,
        // disable the buttons so they cannot move forward
        if (checkedAssetCount === 0) {
            if ($nextBtn.button('instance') !== undefined && $addBtn.button('instance')) {
                $nextBtn.button('disable');
                $addBtn.button('disable');
            } else {
                console.warn('No instance of button found, cannot disable');
            }
        }
    },

    copyGroup: function(roleName, oldName, newName) {
        if (roleName.length > 0 && oldName.length > 0 && newName.length > 0) {
            return axiosInstance
                .get(swa.server + swa.servlet, {
                    params: {
                        _viewClass: 'pgadmin:CopyPermissionGroupService',
                        oldGroup: oldName,
                        newGroup: newName,
                    },
                })
                .then((response) => {
                    window.location =
                        'swp?_viewClass=pgadmin:RoleEditorPage&selectedRole=' +
                        roleName +
                        '&selectedGroup=' +
                        escape(newName);
                })
                .catch((err) => {
                    swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
                });
        }
    },

    createNewGroup: function(roleName, groupName) {
        if (roleName.length > 0 && groupName.length > 0) {
            return axiosInstance
                .get(swa.server + swa.servlet, {
                    params: {
                        _viewClass: 'pgadmin:CreateNewPermissionGroupService',
                        role: roleName,
                        group: groupName,
                        _format: 'json',
                    },
                })
                .then((response) => {
                    window.location =
                        'swp?_viewClass=pgadmin:RoleEditorPage&selectedRole=' +
                        escape(roleName) +
                        '&selectedGroup=' +
                        escape(groupName);
                })
                .catch((err) => {
                    swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
                });
        }
    },

    deleteGroup: function(roleName, groupName) {
        var deleteConfirmed = swa.populateConfirmDialog('Delete group ' + groupName + '?');

        Promise.resolve(deleteConfirmed).then(
            function() {
                var params = {};

                return axiosInstance
                    .get(swa.server + swa.servlet, {
                        params: {
                            _viewClass: 'pgadmin:DeletePermissionGroupService',
                            group: groupName,
                        },
                    })
                    .then((response) => {
                        window.location = 'swp?_viewClass=pgadmin:RoleEditorPage&selectedRole=' + roleName;
                    })
                    .catch((err) => {
                        swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
                    });
            },
            function(err) {
                //dialog cancelled
            }
        );
    },

    /**
     * Turns a div created by pgadmin:Tree into a jsTree.
     * @param id  the id of the div
     * @param link  the URL of the callback for the JSON data
     * @param onLoaded  the event handler for after the tree has been loaded
     * @param onCheck  the event handler for when the selection changes
     * @param onUncheck (depricated)  the event handler for double-clicks on a node
     */
    initTree: function(id, link, onLoaded, onCheck, onUncheck) {
        var $tree = $('#' + id),
            plugins = ['checkbox'],
            link = swa.redirectLink(link),
            params,
            $whatHappened;

        if (swa.server != '') {
            link += '&_server=' + escape(swa.server);
        }
        params = {
            plugins: plugins,
            core: {
                animation: 200,
                themes: { stripes: false },
                data: {
                    dataType: 'json',
                    method: 'get',
                    contentType: 'application/json',
                    url: function(node) {
                        return link;
                    },
                    data: function(node) {
                        var endPoint = node.id === '#' ? '1' : node.id;

                        return {
                            id: endPoint,
                            _bypassCacheDummy: new Date().getTime(),
                        };
                    },
                },
            },
        };

        $tree.jstree(params);

        if (onCheck || onUncheck) {
            $tree.on('changed.jstree', function(event, data) {
                $whatHappened = data.action;

                if ($whatHappened === 'select_node') {
                    eval(onCheck);
                } else if ($whatHappened === 'deselect_node') {
                    eval(onUncheck);
                }
            });
        }

        if (onLoaded) {
            $tree.on('loaded.jstree', function(event, data) {
                eval(onLoaded);
            });
        }
    },

    openAddAssetWildcardDialog: function() {
        $('#addAssetWildcardDialog').dialog('open');
    },

    openCopyGroupDialog: function() {
        $('#copyGroupDialog').dialog('open');
    },

    openCreateNewGroupDialog: function() {
        $('#createNewGroupDialog').dialog('open');
    },

    openRenameGroupDialog: function() {
        $('#renameGroupDialog').dialog('open');
    },

    openSelectAssetsTreeDialog: function(params) {
        if (params != undefined && params != null) {
            swa.load('selectAssetsTreeDialogAssetTree', params);
        }
        $('#selectAssetsTreeDialog').dialog('open');
    },

    openSelectImportClosureForTreeDialog: function(params) {
        swa.load('selectImportClosureForTreeDialogAssets', params);
        $('#selectImportClosureForTreeDialog').dialog('open');
    },

    removeAssetFromGroup: function(assetId, groupName) {
        var removeConfirmed = swa.populateConfirmDialog(
            'Remove resource ' + assetId + ' from group ' + groupName + '?'
        );
        Promise.resolve(removeConfirmed).then(
            function() {
                return axiosInstance
                    .get(swa.server + swa.servlet, {
                        params: {
                            _viewClass: 'pgadmin:RemoveAssetFromGroupService',
                            asset: assetId,
                            group: groupName,
                        },
                    })
                    .then((response) => {
                        swa.load('roleEditorAssetList');
                    })
                    .catch((err) => {
                        swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
                    });
            },
            function(err) {
                //dialog cancelled
            }
        );
    },

    removeRoleFromGroup: function(roleName, groupName) {
        var removeConfirmed = swa.populateConfirmDialog('Remove role ' + roleName + ' from group ' + groupName + '?');
        Promise.resolve(removeConfirmed).then(
            function() {
                return axiosInstance
                    .get(swa.server + swa.servlet, {
                        params: {
                            _viewClass: 'pgadmin:RemoveRoleFromGroupService',
                            role: roleName,
                            group: groupName,
                        },
                    })
                    .then((response) => {
                        swa.load('groupEditorRoleList');
                        swa.load('groupEditorAddRole');
                    })
                    .catch((err) => {
                        swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
                    });
            },
            function(err) {
                //dialog canceled
            }
        );
    },

    removeGroupFromRole: function(roleName, groupName) {
        var removeConfirmed = swa.populateConfirmDialog('Remove group ' + groupName + ' from role ' + roleName + '?');

        Promise.resolve(removeConfirmed).then(
            function() {
                return axiosInstance
                    .get(swa.server + swa.servlet, {
                        params: {
                            _viewClass: 'pgadmin:RemoveRoleFromGroupService',
                            role: roleName,
                            group: groupName,
                        },
                    })
                    .then((response) => {
                        window.location = 'swp?_viewClass=pgadmin:RoleEditorPage&selectedRole=' + roleName;
                    })
                    .catch((err) => {
                        swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
                    });
            },
            function(err) {
                //dialog canceled
            }
        );
    },

    renameGroup: function(roleName, oldName, newName) {
        if (roleName.length > 0 && oldName.length > 0 && newName.length > 0) {
            var params = {};

            return axiosInstance
                .get(swa.server + swa.servlet, {
                    params: {
                        _viewClass: 'pgadmin:RenamePermissionGroupService',
                        oldGroup: oldName,
                        newGroup: newName,
                    },
                })
                .then((response) => {
                    window.location =
                        'swp?_viewClass=pgadmin:RoleEditorPage&selectedRole=' +
                        roleName +
                        '&selectedGroup=' +
                        escape(newName);
                })
                .catch((err) => {
                    swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
                });
        }
    },

    setAssetPermission: function(checkBoxElement, assetID, assetPermission, permissionGroup) {
        return axiosInstance
            .get(swa.server + swa.servlet, {
                params: {
                    _viewClass: 'pgadmin:SetAssetPermissionService',
                    shouldHavePermission: $(checkBoxElement).is(':checked'),
                    assetId: assetID,
                    permission: assetPermission,
                    group: permissionGroup,
                },
            })
            .then((response) => {})
            .catch((err) => {
                swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
            });
    },

    toggleButtonSelect: function(showElementId, hideElementId) {
        $('#' + hideElementId).hide();
        $('#' + showElementId)
            .show()
            .focus();
    },
};

export default pgadmin;
